<template>
    <el-container class="container">
        <el-header class="header" style="text-align: center;line-height: 60px;"></el-header>
        <el-container>
            <el-aside class="aside" width="200px">
                <el-menu
                        :default-active="$route.path"
                        class="el-menu-vertical-demo"
                        background-color="#ccc"
                        :router="true"
                        height="100%">
                    <el-menu-item index="/" :route="{path:'/'}"><i class="el-icon-menu"></i><span slot="title"></span>
                    </el-menu-item>
                    <el-submenu index="1">
                        <template slot="title"><i class="el-icon-location"></i><span></span></template>
                        <el-menu-item index="/workslist" :route="{path:'/workslist'}"></el-menu-item>
                        <el-menu-item index="/articleList" :route="{path:'/articleList'}"></el-menu-item>
                        <el-menu-item index="/videoList" :route="{path:'/videoList'}"></el-menu-item>
                        <el-menu-item index="/recruitmentList" :route="{path:'/recruitmentList'}"></el-menu-item>
                        <el-menu-item index="/resumeList" :route="{path:'/resumeList'}"></el-menu-item>
                    </el-submenu>
                    <el-submenu index="3">
                        <template slot="title"><i class="el-icon-location"></i><span></span></template>
                        <el-menu-item index="/usersList" :route="{path:'/usersList'}"></el-menu-item>
                        <el-menu-item index="/companiesList" :route="{path:'/companiesList'}"></el-menu-item>
                    </el-submenu>
                    <el-submenu index="4">
                    <template slot="title"><i class="el-icon-location"></i><span></span></template>
                    <el-menu-item index="/interviewList" :route="{path:'/interviewList'}"></el-menu-item>
                    <el-menu-item index="/interQuestionList" :route="{path:'/interQuestionList'}"></el-menu-item>
                </el-submenu>
                    <el-submenu index="5">
                        <template slot="title"><i class="el-icon-location"></i><span></span></template>
                        <el-menu-item index="/noticeList" :route="{path:'/noticeList'}"></el-menu-item>
                        <el-menu-item index="/noticeAdd" :route="{path:'/noticeAdd'}"></el-menu-item>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <el-main class="main">
                <slot></slot>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
    export default {
        name: "basicLayout",
        props: ["index"]
    }
</script>

<style scoped>
    .container {
        height: 100%;
        background: #cfcfcf;
    }

    .header {
        height: 20px;
        background: #9f9f9f;;
    }

    .aside {

        background: #ccc;
    }

    .main {
        background: #f3f3f3;
    }
</style>


