<template>
    <basic-layout>
        <!--  ECharts  DOM -->
        <el-col :span="16">
            <div id="main" style="width:600px;height:400px;"></div>
        </el-col>
        <el-col :span="8">
            <div class="box">
                <ul>
                    <li>:&nbsp<span>{{todo.articles}}:&nbsp</span><router-link to="articleList"></router-link></li>
                    <li>:&nbsp<span>{{todo.works}}:&nbsp</span><router-link to="worksList"></router-link></li>
                    <li>:&nbsp<span>{{todo.users}}:&nbsp</span><router-link to="articleList"></router-link></li>
                    <li>:&nbsp<span>{{todo.video}}:&nbsp</span><router-link to="videoList"></router-link></li>
                    <li>:&nbsp<span>{{todo.recruitment}}:&nbsp<router-link to="recruitmentList"></router-link></span></li>
                    <li>:&nbsp<span>{{todo.resume}}:&nbsp</span><router-link to="resumeList"></router-link></li>
                </ul>
            </div>
        </el-col>
    </basic-layout>
</template>

<script>
    import Layout from "./basicLayout"
    import echarts from "echarts"

    export default {
        name: "index",
        components: {
            "basic-layout": Layout,
        },
        data() {
            return {
                count: {},
                data: {},
                todo:{},
            }
        },
        mounted: function () {
            // domecharts
            this.$http.get("https://www.easy-mock.com/mock/5b18ad9ec6b9b923a614ec23/project/count").then(res => {
                this.count = res.data.data.total;
                this.todo=res.data.data.todo;
                let todo=this.todo;
                console.log(todo);
                this.$message({
                    message: "",
                    type: "success",
                })
                var myChart = echarts.init(document.getElementById('main'));
                let data = this.count;
                console.log(data.users);
                var option = {
                    title: {
                        text: 'ECharts '
                    },
                    tooltip: {},
                    legend: {
                        data: ['']
                    },
                    xAxis: {
                        data: ["", "", "", "", "", ""]
                    },
                    yAxis: {},
                    series: [{
                        name: '',
                        type: 'bar',
                        data: [data.articles, data.works, data.users, data.video, data.recruitment, data.resume]
                    }]
                };
                myChart.setOption(option);
            });

        }
    }
</script>

<style scoped>
ul li {
    height:30px;
    border-bottom:1px #ccc solid;
}
    span {
        color: #1c5b7e;
    }
    .box {
        height:auto;
        width:auto;
        margin:50px  auto;

    }
</style>


