<template>
    <basic-layout>
        <el-table
                :data="tableData"
                :stripe="true"
                :border="true"
                style="width: 100%">
            <el-table-column
                    prop="id"
                    label="id"
                    width="100"
                    align="center"
            >
            </el-table-column>
            <el-table-column
                    prop="name"
                    label=""
                    width="80"
                    align="center"
            >
            </el-table-column>
            <el-table-column
                    prop="sex"
                    label=""
                    width="80"
                    align="center"
            >
            </el-table-column>
            <el-table-column
                    prop="position"
                    label=""
                    width="100"
                    align="center"
            >
            </el-table-column>
            <el-table-column
                    prop="exp"
                    label=""
                    width="80"
                    align="center"
            >
            </el-table-column>
            <el-table-column
                    prop="record"
                    label=""
                    width="80"
                    align="center"
            >
            </el-table-column>
            <el-table-column
                    prop="nowposition"
                    label=""
                    width="100"
                    align="center"
            >
            </el-table-column>
            <el-table-column
                    prop="publicDate"
                    label=""
                    width="160"
                    align="center"
                    :sortable="true"
            >
            </el-table-column>
            <el-table-column
                    prop="state"
                    label=""
                    width="80"
                    align="center"
            >
            </el-table-column>
            <el-table-column
                    label=""
                    width="120"
                    align="center"
            >
                <template slot-scope="scope">
                    <el-button type="primary" plain v-if="scope.row.state===''" @click="audit(scope.row)">
                    </el-button>
                    <el-button type="success" plain v-else-if="scope.row.state===''"><a href=""></a></el-button>
                    <el-button type="warning" plain v-else @click="audit(scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-row type="flex" justify="center" style="margin-top:30px;">
            <el-pagination
                    background
                    layout="prev, pager, next"
                    :page-size="4"
                    :current-page.sync="now"
                    :total="total"
                    @current-change="change"
            >
            </el-pagination>
        </el-row>
        <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="30%"
        >
            <div>
                <el-select v-model="value" placeholder="">
                    <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </div>
            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
    <el-button type="primary" @click="submit"> </el-button>
  </span>
        </el-dialog>
    </basic-layout>
</template>

<script>
    import Layout from "../basicLayout"

    export default {
        name: "resumelist",
        components: {
            "basic-layout": Layout
        },
        data() {
            return {
                tableData: [],
                dialogVisible: false,
                obj: [],
                total: 0,
                now: 1,
                options: [{
                    value: 'pass',
                    label: ''
                }, {
                    value: 'fail',
                    label: ''
                }],
                value: ''
            }
        },
        methods: {
            audit(obj) {
                this.obj = obj;
                this.dialogVisible = true;
            },
            getData(page = 1) {
                this.$http.get(`admin/getResumes?p=${page}`).then(res => {
                    if (res.status === 200) {
                        this.total = res.body.total;
                        let data = res.body.data.map(obj => {
                            if (obj.state === "pass") {
                                obj.state = "";
                            } else if (obj.state === "fail") {
                                obj.state = "";
                            } else if (obj.state === "unauditing") {
                                obj.state = "";
                            }
                            return obj;
                        });
                        this.tableData = data;
                        this.$message({
                            message: "",
                            type: "success"
                        })
                    }
                })

            },
            change: function (page) {
                this.getData(page);

            },
            submit() {
                this.dialogVisible = false;
                if (this.value === 'pass') {
                    this.$http.get(`/admin/auditingResume?id=${this.obj.id}&state=${"pass"}`).then(res => {
                        if (res.data.data.message === "ok") {
                            this.$message({
                                message: "",
                                type: "success"
                            });
                            this.obj.state = ""
                        }
                    });
                } else if (this.value === "fail") {
                    this.$http.get(`/admin/auditingResume?id=${this.obj.id}&state=${"fail"}`).then(res => {
                        if (res.data.data.message === "ok") {
                            this.$message({
                                message: "",
                                type: "success"
                            });
                            this.obj.state = ""
                        }
                    });
                }
                this.value = "";
            },
        },
            mounted() {
                this.getData();
            }
        }
</script>

<style scoped>

</style>


